import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { Cookies } from 'react-cookie';
import styled from 'styled-components';
// import RegisterBox from './RegisterBox';
import CSRFToken from '../etc/CSRFToken';
import PlaceBoxRegister from './PlaceBoxRegister';
import { useNavigate } from 'react-router-dom';

const Button = styled.div`
    all: unset;
    border: 1px solid orange;
    border-radius: 10px;
    padding: 5px;
    color: black;
    font-size: 13px;
`;
const Input = styled.input`
    &:focus {
        outline: 2px solid orange;
    }
`;
function SearchData() {
    const [value, setValue] = useState({
        place: '',
    });
    const [review, setReview] = useState({
        description: '',
    });

    const [datas, setDatas] = useState([]);
    // const [list, setList] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    // const [resName, setResName] = useState(`${value.place}`);
    const [displayButton, setDisplayButton] = useState('inline-block');
    const [displayRegisterForm, setDisplayRegisterForm] = useState('none');
    const handleClick = () => {
        setDisplayButton('none');
        setDisplayRegisterForm('block');
    };
    const handleChange = (e) => {
        setValue({
            ...value,
            [e.target.name]: e.target.value,
        });
    };
    const handleDescription = (e) => {
        setReview(e.target.value);
    };
    // const handleNameChange = (e) => {
    //     setResName(e.target.value);
    // };

    const fetchData = async () => {
        if (value.place === '') return;

        const formData = new FormData();
        formData.append('place', value.place);

        const cookies = new Cookies();

        // const setCookie = (name, value, option) => {
        //     return cookies.set(name, value, { ...option });
        // };
        const getCookie = (name) => {
            return cookies.get(name);
        };
        // const csrftoken = getCookie('csrftoken');
        const response = await axios({
            method: 'post',
            url: 'http://localhost:8000/api/v1/places/search_place/',
            data: formData,
            // headers: { authorization: `Bearer ${getCookie('place')}` },
        })
            .then((response) => {
                console.log('response.data', response.data);
                setDatas(response.data);
                console.log(datas);
                setIsLoading(false);
                // const accessToken = response.data.token;
                // setCookie('place', accessToken);
            })
            .catch((error) => {
                console.log('');
                console.log(error);
            });
    };
    const handleSubmit = (e) => {
        e.preventDefault();
        fetchData();
    };

    useEffect(() => {
        fetchData();
    }, []);

    // csrftoken header        !!!!!!!!!!1
    // const getDatas = async () => {
    //     const response = await fetch('http://localhost:8000/api/v1/places/');
    //     const json = await response.json();

    //     setDatas(json);
    //     setIsLoading(false);
    //     console.log(json[0]?.title);
    // };
    const navigate = useNavigate();

    const handleRegister = (e) => {
        e.preventDefault();
        console.log(e.target);
        const place = {
            title: e.target.title.value,
            category: e.target.category.value,
            link: e.target.link.value,
            image: e.target.image.value,
        };
        console.log(place);
        const places = axios.postForm(
            'http://localhost:8000/api/v1/places/create_place/',
            place
        );
        navigate('/', { replace: true });
    }; /////   
    // const handleRegister = async (e) => {
    //     e.preventDefault();

    //     const response = await fetch('http://localhost:8000/api/v1/places/');
    //     const json = await response.json();

    //     setList(json);
    //     // setIsLoading(false);
    //     console.log(json[0]?.title);
    // };

    // useEffect(() => {
    //     async function fetchData() {
    //         const response = await fetch(
    //             'http://localhost:8000/api/v1/places/'
    //         );
    //         const json = await response.json();

    //         setList(json);
    //         // setIsLoading(false);
    //         // console.log(json[0]?.title);
    //     }

    //     fetchData();
    // }, []); ////    ..    

    // console.log('datas', datas); //     .   

    // const handleRegister = async (e) => {
    //     e.preventDefault();
    //     const formData = new FormData();
    //     formData.append('title', datas.place.items[0].title);
    //     formData.append('category', datas.place.items[0].category);
    //     formData.append(
    //         'link',
    //         'https://map.naver.com/v5/search/' + value.place
    //     );
    //     formData.append('image', datas.image.items[0].link);
    //     formData.append('description', value.description);
    //     fetch('http://localhost:8000/api/v1/places/create_place/', {
    //         method: 'POST',
    //         cache: 'no-cache',
    //         body: formData, // body    
    //     })
    //         .then((response) => response.json())
    //         .then((data) => {
    //             console.log(data);
    //         });
    // }; //     !@!!!!!!!!1

    // const blob = new Blob([JSON.stringify(dataObj)], {
    //     type: 'application/json',
    //   });
    //   formData.append('info', blob)

    const createApi = 'http://localhost:8000/api/v1/places/create_place/';

    return (
        <div>
            <div
                className="container"
                style={{
                    justifyContent: 'center',
                    position: 'absolute',
                    top: 0,
                    bottom: 0,
                    left: 0,
                }}
            >
                <form
                    method="post"
                    style={{
                        width: '320px',
                        display: 'flex',
                        justifyContent: 'center',
                        alignItems: 'center',
                        position: 'relative',
                        backgroundColor: 'white',
                        height: '50px',
                    }}
                    onSubmit={handleSubmit}
                >
                    <CSRFToken />
                    <div
                        style={{
                            position: 'absolute',
                            width: '290px',
                            display: 'flex',
                            justifyContent: 'space-between',
                        }}
                    >
                        <Input
                            style={{
                                borderRadius: '10px',
                                border: '1px solid black',
                                height: '20px',
                                width: '100%',
                                maxWidth: '235px',
                                marginRight: 5,
                                top: 10,
                                border: '1px solid orange',
                            }}
                            name="place"
                            onChange={handleChange}
                            value={value.place}
                            type="text"
                        ></Input>
                        <Button
                            type="submit"
                            onClick={handleSubmit}
                            style={{ pointer: 'cursor' }}
                        >
                            
                        </Button>
                    </div>
                </form>
                <div>
                    {isLoading ? (
                        <h1 style={{ marginLeft: 15 }}>
                              !
                        </h1>
                    ) : Object.keys(datas).length ? (
                        <PlaceBoxRegister
                            brandRunTime="24"
                            likeNum="6"
                            disLikeNum="2"
                            placeLink=""
                            res_name={datas.place.items[0].title}
                            res_img={datas.image.items[0].link}
                            res_category={datas.place.items[0].category}
                            res_link={
                                'https://map.naver.com/v5/search/' + value.place
                            }
                            handleClick={handleClick}
                            displayButton={displayButton}
                        />
                    ) : (
                        <h1> .</h1>
                    )}
                </div>
                <div style={{ display: displayRegisterForm }}>
                    <form
                        method="post"
                        action={createApi}
                        onSubmit={handleRegister}
                        style={{
                            display: 'flex',
                            justifyContent: 'center',
                            alignContent: 'center',
                        }}
                    >
                        {Object.keys(datas).length && (
                            <div
                                style={{
                                    display: 'flex',
                                    flexDirection: 'column',
                                }}
                            >
                                <Input
                                    type="text"
                                    name="title"
                                    value={datas.place.items[0].title}
                                    hidden
                                    // onChange={handleNameChange}
                                    style={{
                                        width: '290px',
                                        marginBottom: '10px',
                                        marginTop: '10px',
                                        borderRadius: '7px',
                                        height: '20px',
                                        border: '1px solid orange',
                                    }}
                                    // placeholder={`${value.place}`}
                                ></Input>
                                <Input
                                    type="text"
                                    name="description"
                                    value={review.description}
                                    onChange={handleDescription}
                                    placeholder=" : ,  : "
                                    style={{
                                        width: '290px',
                                        height: '100px',
                                        borderRadius: '8px',
                                        border: '1px solid orange',
                                    }}
                                />
                                <input
                                    type="text"
                                    name="category"
                                    value={datas.place.items[0].category}
                                ></input>
                                <input
                                    type="text"
                                    name="image"
                                    value={datas.image.items[0].link}
                                />
                                <input
                                    type="text"
                                    name="link"
                                    value={
                                        'https://map.naver.com/v5/search/' +
                                        value.place
                                    }
                                />

                                <div
                                    style={{
                                        display: 'inline-block',
                                        marginLeft: '253px',
                                        marginTop: '10px',
                                    }}
                                >
                                    <button></button>
                                </div>
                            </div>
                        )}
                    </form>
                </div>
            </div>
        </div>
    );
}
export default SearchData;
